/* prefix ".codeblock-theme-tomorrow" added by @codeblock/themes */
/**
 * prism.js tomorrow night eighties for JavaScript, CoffeeScript, CSS and HTML
 * Based on https://github.com/chriskempson/tomorrow-theme
 * @author Rose Pritchard
 */
.codeblock-theme-tomorrow code[class*="language-"],.codeblock-theme-tomorrow 
pre[class*="language-"] {
    color: #ccc;
    background: none;
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: 1em;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}

/* Code blocks */
.codeblock-theme-tomorrow pre[class*="language-"] {
    padding: 1em;
    margin: .5em 0;
    overflow: auto;
}

.codeblock-theme-tomorrow :not(pre) > code[class*="language-"],.codeblock-theme-tomorrow 
pre[class*="language-"] {
    background: #2d2d2d;
}

/* Inline code */
.codeblock-theme-tomorrow :not(pre) > code[class*="language-"] {
    padding: .1em;
    border-radius: .3em;
    white-space: normal;
}

.codeblock-theme-tomorrow .token.comment,.codeblock-theme-tomorrow 
.token.block-comment,.codeblock-theme-tomorrow 
.token.prolog,.codeblock-theme-tomorrow 
.token.doctype,.codeblock-theme-tomorrow 
.token.cdata {
    color: #999;
}

.codeblock-theme-tomorrow .token.punctuation {
    color: #ccc;
}

.codeblock-theme-tomorrow .token.tag,.codeblock-theme-tomorrow 
.token.attr-name,.codeblock-theme-tomorrow 
.token.namespace,.codeblock-theme-tomorrow 
.token.deleted {
    color: #e2777a;
}

.codeblock-theme-tomorrow .token.function-name {
    color: #6196cc;
}

.codeblock-theme-tomorrow .token.boolean,.codeblock-theme-tomorrow 
.token.number,.codeblock-theme-tomorrow 
.token.function {
    color: #f08d49;
}

.codeblock-theme-tomorrow .token.property,.codeblock-theme-tomorrow 
.token.class-name,.codeblock-theme-tomorrow 
.token.constant,.codeblock-theme-tomorrow 
.token.symbol {
    color: #f8c555;
}

.codeblock-theme-tomorrow .token.selector,.codeblock-theme-tomorrow 
.token.important,.codeblock-theme-tomorrow 
.token.atrule,.codeblock-theme-tomorrow 
.token.keyword,.codeblock-theme-tomorrow 
.token.builtin {
    color: #cc99cd;
}

.codeblock-theme-tomorrow .token.string,.codeblock-theme-tomorrow 
.token.char,.codeblock-theme-tomorrow 
.token.attr-value,.codeblock-theme-tomorrow 
.token.regex,.codeblock-theme-tomorrow 
.token.variable {
    color: #7ec699;
}

.codeblock-theme-tomorrow .token.operator,.codeblock-theme-tomorrow 
.token.entity,.codeblock-theme-tomorrow 
.token.url {
    color: #67cdcc;
}

.codeblock-theme-tomorrow .token.important,.codeblock-theme-tomorrow 
.token.bold {
    font-weight: bold;
}

.codeblock-theme-tomorrow .token.italic {
    font-style: italic;
}

.codeblock-theme-tomorrow .token.entity {
    cursor: help;
}

.codeblock-theme-tomorrow .token.inserted {
    color: green;
}

