/* prefix ".codeblock-theme-prism" added by @codeblock/themes */
/**
 * prism.js default theme for JavaScript, CSS and HTML
 * Based on dabblet (http://dabblet.com)
 * @author Lea Verou
 */
.codeblock-theme-prism code[class*="language-"],.codeblock-theme-prism 
pre[class*="language-"] {
    color: black;
    background: none;
    text-shadow: 0 1px white;
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: 1em;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}

.codeblock-theme-prism pre[class*="language-"]::-moz-selection,.codeblock-theme-prism  pre[class*="language-"] ::-moz-selection,.codeblock-theme-prism 
code[class*="language-"]::-moz-selection,.codeblock-theme-prism  code[class*="language-"] ::-moz-selection {
    text-shadow: none;
    background: #b3d4fc;
}

.codeblock-theme-prism pre[class*="language-"]::selection,.codeblock-theme-prism  pre[class*="language-"] ::selection,.codeblock-theme-prism 
code[class*="language-"]::selection,.codeblock-theme-prism  code[class*="language-"] ::selection {
    text-shadow: none;
    background: #b3d4fc;
}

@media print{
    .codeblock-theme-prism code[class*="language-"],.codeblock-theme-prism 
	pre[class*="language-"] {
        text-shadow: none;
    }

}

/* Code blocks */
.codeblock-theme-prism pre[class*="language-"] {
    padding: 1em;
    margin: .5em 0;
    overflow: auto;
}

.codeblock-theme-prism :not(pre) > code[class*="language-"],.codeblock-theme-prism 
pre[class*="language-"] {
    background: #f5f2f0;
}

/* Inline code */
.codeblock-theme-prism :not(pre) > code[class*="language-"] {
    padding: .1em;
    border-radius: .3em;
    white-space: normal;
}

.codeblock-theme-prism .token.comment,.codeblock-theme-prism 
.token.prolog,.codeblock-theme-prism 
.token.doctype,.codeblock-theme-prism 
.token.cdata {
    color: slategray;
}

.codeblock-theme-prism .token.punctuation {
    color: #999;
}

.codeblock-theme-prism .token.namespace {
    opacity: .7;
}

.codeblock-theme-prism .token.property,.codeblock-theme-prism 
.token.tag,.codeblock-theme-prism 
.token.boolean,.codeblock-theme-prism 
.token.number,.codeblock-theme-prism 
.token.constant,.codeblock-theme-prism 
.token.symbol,.codeblock-theme-prism 
.token.deleted {
    color: #905;
}

.codeblock-theme-prism .token.selector,.codeblock-theme-prism 
.token.attr-name,.codeblock-theme-prism 
.token.string,.codeblock-theme-prism 
.token.char,.codeblock-theme-prism 
.token.builtin,.codeblock-theme-prism 
.token.inserted {
    color: #690;
}

.codeblock-theme-prism .token.operator,.codeblock-theme-prism 
.token.entity,.codeblock-theme-prism 
.token.url,.codeblock-theme-prism 
.language-css .token.string,.codeblock-theme-prism 
.style .token.string {
    color: #9a6e3a;
    background: hsla(0, 0%, 100%, .5);
}

.codeblock-theme-prism .token.atrule,.codeblock-theme-prism 
.token.attr-value,.codeblock-theme-prism 
.token.keyword {
    color: #07a;
}

.codeblock-theme-prism .token.function,.codeblock-theme-prism 
.token.class-name {
    color: #DD4A68;
}

.codeblock-theme-prism .token.regex,.codeblock-theme-prism 
.token.important,.codeblock-theme-prism 
.token.variable {
    color: #e90;
}

.codeblock-theme-prism .token.important,.codeblock-theme-prism 
.token.bold {
    font-weight: bold;
}

.codeblock-theme-prism .token.italic {
    font-style: italic;
}

.codeblock-theme-prism .token.entity {
    cursor: help;
}

