/* prefix ".codeblock-theme-twilight" added by @codeblock/themes */
/**
 * prism.js Twilight theme
 * Based (more or less) on the Twilight theme originally of Textmate fame.
 * @author Remy Bach
 */
.codeblock-theme-twilight code[class*="language-"],.codeblock-theme-twilight 
pre[class*="language-"] {
    color: white;
    background: none;
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: 1em;
    text-align: left;
    text-shadow: 0 -.1em .2em black;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}

.codeblock-theme-twilight pre[class*="language-"],.codeblock-theme-twilight 
:not(pre) > code[class*="language-"] {
    background: hsl(0, 0%, 8%);
    /* #141414 */;
}

/* Code blocks */
.codeblock-theme-twilight pre[class*="language-"] {
    border-radius: .5em;
    border: .3em solid hsl(0, 0%, 33%);
    /* #282A2B */
	box-shadow: 1px 1px .5em black inset;
    margin: .5em 0;
    overflow: auto;
    padding: 1em;
}

.codeblock-theme-twilight pre[class*="language-"]::-moz-selection {
    /* Firefox */
	background: hsl(200, 4%, 16%);
    /* #282A2B */;
}

.codeblock-theme-twilight pre[class*="language-"]::selection {
    /* Safari */
	background: hsl(200, 4%, 16%);
    /* #282A2B */;
}

/* Text Selection colour */
.codeblock-theme-twilight pre[class*="language-"]::-moz-selection,.codeblock-theme-twilight  pre[class*="language-"] ::-moz-selection,.codeblock-theme-twilight 
code[class*="language-"]::-moz-selection,.codeblock-theme-twilight  code[class*="language-"] ::-moz-selection {
    text-shadow: none;
    background: hsla(0, 0%, 93%, 0.15);
    /* #EDEDED */;
}

.codeblock-theme-twilight pre[class*="language-"]::selection,.codeblock-theme-twilight  pre[class*="language-"] ::selection,.codeblock-theme-twilight 
code[class*="language-"]::selection,.codeblock-theme-twilight  code[class*="language-"] ::selection {
    text-shadow: none;
    background: hsla(0, 0%, 93%, 0.15);
    /* #EDEDED */;
}

/* Inline code */
.codeblock-theme-twilight :not(pre) > code[class*="language-"] {
    border-radius: .3em;
    border: .13em solid hsl(0, 0%, 33%);
    /* #545454 */
	box-shadow: 1px 1px .3em -.1em black inset;
    padding: .15em .2em .05em;
    white-space: normal;
}

.codeblock-theme-twilight .token.comment,.codeblock-theme-twilight 
.token.prolog,.codeblock-theme-twilight 
.token.doctype,.codeblock-theme-twilight 
.token.cdata {
    color: hsl(0, 0%, 47%);
    /* #777777 */;
}

.codeblock-theme-twilight .token.punctuation {
    opacity: .7;
}

.codeblock-theme-twilight .token.namespace {
    opacity: .7;
}

.codeblock-theme-twilight .token.tag,.codeblock-theme-twilight 
.token.boolean,.codeblock-theme-twilight 
.token.number,.codeblock-theme-twilight 
.token.deleted {
    color: hsl(14, 58%, 55%);
    /* #CF6A4C */;
}

.codeblock-theme-twilight .token.keyword,.codeblock-theme-twilight 
.token.property,.codeblock-theme-twilight 
.token.selector,.codeblock-theme-twilight 
.token.constant,.codeblock-theme-twilight 
.token.symbol,.codeblock-theme-twilight 
.token.builtin {
    color: hsl(53, 89%, 79%);
    /* #F9EE98 */;
}

.codeblock-theme-twilight .token.attr-name,.codeblock-theme-twilight 
.token.attr-value,.codeblock-theme-twilight 
.token.string,.codeblock-theme-twilight 
.token.char,.codeblock-theme-twilight 
.token.operator,.codeblock-theme-twilight 
.token.entity,.codeblock-theme-twilight 
.token.url,.codeblock-theme-twilight 
.language-css .token.string,.codeblock-theme-twilight 
.style .token.string,.codeblock-theme-twilight 
.token.variable,.codeblock-theme-twilight 
.token.inserted {
    color: hsl(76, 21%, 52%);
    /* #8F9D6A */;
}

.codeblock-theme-twilight .token.atrule {
    color: hsl(218, 22%, 55%);
    /* #7587A6 */;
}

.codeblock-theme-twilight .token.regex,.codeblock-theme-twilight 
.token.important {
    color: hsl(42, 75%, 65%);
    /* #E9C062 */;
}

.codeblock-theme-twilight .token.important,.codeblock-theme-twilight 
.token.bold {
    font-weight: bold;
}

.codeblock-theme-twilight .token.italic {
    font-style: italic;
}

.codeblock-theme-twilight .token.entity {
    cursor: help;
}

.codeblock-theme-twilight pre[data-line] {
    padding: 1em 0 1em 3em;
    position: relative;
}

/* Markup */
.codeblock-theme-twilight .language-markup .token.tag,.codeblock-theme-twilight 
.language-markup .token.attr-name,.codeblock-theme-twilight 
.language-markup .token.punctuation {
    color: hsl(33, 33%, 52%);
    /* #AC885B */;
}

/* Make the tokens sit above the line highlight so the colours don't look faded. */
.codeblock-theme-twilight .token {
    position: relative;
    z-index: 1;
}

.codeblock-theme-twilight .line-highlight {
    background: hsla(0, 0%, 33%, 0.25);
    /* #545454 */
	background: linear-gradient(to right, hsla(0, 0%, 33%, .1) 70%, hsla(0, 0%, 33%, 0));
    /* #545454 */
	border-bottom: 1px dashed hsl(0, 0%, 33%);
    /* #545454 */
	border-top: 1px dashed hsl(0, 0%, 33%);
    /* #545454 */
	left: 0;
    line-height: inherit;
    margin-top: 0.75em;
    /* Same as .prism’s padding-top */
	padding: inherit 0;
    pointer-events: none;
    position: absolute;
    right: 0;
    white-space: pre;
    z-index: 0;
}

.codeblock-theme-twilight .line-highlight:before,.codeblock-theme-twilight 
.line-highlight[data-end]:after {
    background-color: hsl(215, 15%, 59%);
    /* #8794A6 */
	border-radius: 999px;
    box-shadow: 0 1px white;
    color: hsl(24, 20%, 95%);
    /* #F5F2F0 */
	content: attr(data-start);
    font: bold 65%/1.5 sans-serif;
    left: .6em;
    min-width: 1em;
    padding: 0 .5em;
    position: absolute;
    text-align: center;
    text-shadow: none;
    top: .4em;
    vertical-align: .3em;
}

.codeblock-theme-twilight .line-highlight[data-end]:after {
    bottom: .4em;
    content: attr(data-end);
    top: auto;
}

