/* prefix ".codeblock-theme-dark" added by @codeblock/themes */
.codeblock-theme-dark /**
 * prism.js Dark theme for JavaScript,.codeblock-theme-dark  CSS and HTML
 * Based on the slides of the talk “/Reg(exp) {
    2;
}

.codeblock-theme-dark lained/”
 * @author Lea Verou
 */

code[class*="language-"],.codeblock-theme-dark 
pre[class*="language-"] {
    color: white;
    background: none;
    text-shadow: 0 -.1em .2em black;
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: 1em;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}

@media print{
    .codeblock-theme-dark code[class*="language-"],.codeblock-theme-dark 
	pre[class*="language-"] {
        text-shadow: none;
    }

}

.codeblock-theme-dark pre[class*="language-"],.codeblock-theme-dark 
:not(pre) > code[class*="language-"] {
    background: hsl(30, 20%, 25%);
}

/* Code blocks */
.codeblock-theme-dark pre[class*="language-"] {
    padding: 1em;
    margin: .5em 0;
    overflow: auto;
    border: .3em solid hsl(30, 20%, 40%);
    border-radius: .5em;
    box-shadow: 1px 1px .5em black inset;
}

/* Inline code */
.codeblock-theme-dark :not(pre) > code[class*="language-"] {
    padding: .15em .2em .05em;
    border-radius: .3em;
    border: .13em solid hsl(30, 20%, 40%);
    box-shadow: 1px 1px .3em -.1em black inset;
    white-space: normal;
}

.codeblock-theme-dark .token.comment,.codeblock-theme-dark 
.token.prolog,.codeblock-theme-dark 
.token.doctype,.codeblock-theme-dark 
.token.cdata {
    color: hsl(30, 20%, 50%);
}

.codeblock-theme-dark .token.punctuation {
    opacity: .7;
}

.codeblock-theme-dark .token.namespace {
    opacity: .7;
}

.codeblock-theme-dark .token.property,.codeblock-theme-dark 
.token.tag,.codeblock-theme-dark 
.token.boolean,.codeblock-theme-dark 
.token.number,.codeblock-theme-dark 
.token.constant,.codeblock-theme-dark 
.token.symbol {
    color: hsl(350, 40%, 70%);
}

.codeblock-theme-dark .token.selector,.codeblock-theme-dark 
.token.attr-name,.codeblock-theme-dark 
.token.string,.codeblock-theme-dark 
.token.char,.codeblock-theme-dark 
.token.builtin,.codeblock-theme-dark 
.token.inserted {
    color: hsl(75, 70%, 60%);
}

.codeblock-theme-dark .token.operator,.codeblock-theme-dark 
.token.entity,.codeblock-theme-dark 
.token.url,.codeblock-theme-dark 
.language-css .token.string,.codeblock-theme-dark 
.style .token.string,.codeblock-theme-dark 
.token.variable {
    color: hsl(40, 90%, 60%);
}

.codeblock-theme-dark .token.atrule,.codeblock-theme-dark 
.token.attr-value,.codeblock-theme-dark 
.token.keyword {
    color: hsl(350, 40%, 70%);
}

.codeblock-theme-dark .token.regex,.codeblock-theme-dark 
.token.important {
    color: #e90;
}

.codeblock-theme-dark .token.important,.codeblock-theme-dark 
.token.bold {
    font-weight: bold;
}

.codeblock-theme-dark .token.italic {
    font-style: italic;
}

.codeblock-theme-dark .token.entity {
    cursor: help;
}

.codeblock-theme-dark .token.deleted {
    color: red;
}

