/* prefix ".codeblock-theme-okaidia" added by @codeblock/themes */
/**
 * okaidia theme for JavaScript, CSS and HTML
 * Loosely based on Monokai textmate theme by http://www.monokai.nl/
 * @author ocodia
 */
.codeblock-theme-okaidia code[class*="language-"],.codeblock-theme-okaidia 
pre[class*="language-"] {
    color: #f8f8f2;
    background: none;
    text-shadow: 0 1px rgba(0, 0, 0, 0.3);
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: 1em;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}

/* Code blocks */
.codeblock-theme-okaidia pre[class*="language-"] {
    padding: 1em;
    margin: .5em 0;
    overflow: auto;
    border-radius: 0.3em;
}

.codeblock-theme-okaidia :not(pre) > code[class*="language-"],.codeblock-theme-okaidia 
pre[class*="language-"] {
    background: #272822;
}

/* Inline code */
.codeblock-theme-okaidia :not(pre) > code[class*="language-"] {
    padding: .1em;
    border-radius: .3em;
    white-space: normal;
}

.codeblock-theme-okaidia .token.comment,.codeblock-theme-okaidia 
.token.prolog,.codeblock-theme-okaidia 
.token.doctype,.codeblock-theme-okaidia 
.token.cdata {
    color: slategray;
}

.codeblock-theme-okaidia .token.punctuation {
    color: #f8f8f2;
}

.codeblock-theme-okaidia .token.namespace {
    opacity: .7;
}

.codeblock-theme-okaidia .token.property,.codeblock-theme-okaidia 
.token.tag,.codeblock-theme-okaidia 
.token.constant,.codeblock-theme-okaidia 
.token.symbol,.codeblock-theme-okaidia 
.token.deleted {
    color: #f92672;
}

.codeblock-theme-okaidia .token.boolean,.codeblock-theme-okaidia 
.token.number {
    color: #ae81ff;
}

.codeblock-theme-okaidia .token.selector,.codeblock-theme-okaidia 
.token.attr-name,.codeblock-theme-okaidia 
.token.string,.codeblock-theme-okaidia 
.token.char,.codeblock-theme-okaidia 
.token.builtin,.codeblock-theme-okaidia 
.token.inserted {
    color: #a6e22e;
}

.codeblock-theme-okaidia .token.operator,.codeblock-theme-okaidia 
.token.entity,.codeblock-theme-okaidia 
.token.url,.codeblock-theme-okaidia 
.language-css .token.string,.codeblock-theme-okaidia 
.style .token.string,.codeblock-theme-okaidia 
.token.variable {
    color: #f8f8f2;
}

.codeblock-theme-okaidia .token.atrule,.codeblock-theme-okaidia 
.token.attr-value,.codeblock-theme-okaidia 
.token.function,.codeblock-theme-okaidia 
.token.class-name {
    color: #e6db74;
}

.codeblock-theme-okaidia .token.keyword {
    color: #66d9ef;
}

.codeblock-theme-okaidia .token.regex,.codeblock-theme-okaidia 
.token.important {
    color: #fd971f;
}

.codeblock-theme-okaidia .token.important,.codeblock-theme-okaidia 
.token.bold {
    font-weight: bold;
}

.codeblock-theme-okaidia .token.italic {
    font-style: italic;
}

.codeblock-theme-okaidia .token.entity {
    cursor: help;
}

