/* prefix ".codeblock-theme-funky" added by @codeblock/themes */
/**
 * prism.js Funky theme
 * Based on “Polyfilling the gaps” talk slides http://lea.verou.me/polyfilling-the-gaps/
 * @author Lea Verou
 */
.codeblock-theme-funky code[class*="language-"],.codeblock-theme-funky 
pre[class*="language-"] {
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: 1em;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}

/* Code blocks */
.codeblock-theme-funky pre[class*="language-"] {
    padding: .4em .8em;
    margin: .5em 0;
    overflow: auto;
    background: url('data:image/svg+xml;charset=utf-8,<svg%20version%3D"1.1"%20xmlns%3D"http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg"%20width%3D"100"%20height%3D"100"%20fill%3D"rgba(0%2C0%2C0%2C.2)">%0D%0A<polygon%20points%3D"0%2C50%2050%2C0%200%2C0"%20%2F>%0D%0A<polygon%20points%3D"0%2C100%2050%2C100%20100%2C50%20100%2C0"%20%2F>%0D%0A<%2Fsvg>');
    background-size: 1em 1em;
}

.codeblock-theme-funky code[class*="language-"] {
    background: black;
    color: white;
    box-shadow: -.3em 0 0 .3em black, .3em 0 0 .3em black;
}

/* Inline code */
.codeblock-theme-funky :not(pre) > code[class*="language-"] {
    padding: .2em;
    border-radius: .3em;
    box-shadow: none;
    white-space: normal;
}

.codeblock-theme-funky .token.comment,.codeblock-theme-funky 
.token.prolog,.codeblock-theme-funky 
.token.doctype,.codeblock-theme-funky 
.token.cdata {
    color: #aaa;
}

.codeblock-theme-funky .token.punctuation {
    color: #999;
}

.codeblock-theme-funky .token.namespace {
    opacity: .7;
}

.codeblock-theme-funky .token.property,.codeblock-theme-funky 
.token.tag,.codeblock-theme-funky 
.token.boolean,.codeblock-theme-funky 
.token.number,.codeblock-theme-funky 
.token.constant,.codeblock-theme-funky 
.token.symbol {
    color: #0cf;
}

.codeblock-theme-funky .token.selector,.codeblock-theme-funky 
.token.attr-name,.codeblock-theme-funky 
.token.string,.codeblock-theme-funky 
.token.char,.codeblock-theme-funky 
.token.builtin {
    color: yellow;
}

.codeblock-theme-funky .token.operator,.codeblock-theme-funky 
.token.entity,.codeblock-theme-funky 
.token.url,.codeblock-theme-funky 
.language-css .token.string,.codeblock-theme-funky 
.token.variable,.codeblock-theme-funky 
.token.inserted {
    color: yellowgreen;
}

.codeblock-theme-funky .token.atrule,.codeblock-theme-funky 
.token.attr-value,.codeblock-theme-funky 
.token.keyword {
    color: deeppink;
}

.codeblock-theme-funky .token.regex,.codeblock-theme-funky 
.token.important {
    color: orange;
}

.codeblock-theme-funky .token.important,.codeblock-theme-funky 
.token.bold {
    font-weight: bold;
}

.codeblock-theme-funky .token.italic {
    font-style: italic;
}

.codeblock-theme-funky .token.entity {
    cursor: help;
}

.codeblock-theme-funky .token.deleted {
    color: red;
}

/* Plugin styles: Diff Highlight */
.codeblock-theme-funky pre.diff-highlight.diff-highlight > code .token.deleted:not(.prefix),.codeblock-theme-funky 
pre > code.diff-highlight.diff-highlight .token.deleted:not(.prefix) {
    background-color: rgba(255, 0, 0, .3);
    display: inline;
}

.codeblock-theme-funky pre.diff-highlight.diff-highlight > code .token.inserted:not(.prefix),.codeblock-theme-funky 
pre > code.diff-highlight.diff-highlight .token.inserted:not(.prefix) {
    background-color: rgba(0, 255, 128, .3);
    display: inline;
}

