/* prefix ".codeblock-theme-solarizedlight" added by @codeblock/themes */
/*
 Solarized Color Schemes originally by Ethan Schoonover
 http://ethanschoonover.com/solarized

 Ported for PrismJS by Hector Matos
 Website: https://krakendev.io
 Twitter Handle: https://twitter.com/allonsykraken)
*/
.codeblock-theme-solarizedlight code[class*="language-"],.codeblock-theme-solarizedlight 
pre[class*="language-"] {
    color: #657b83;
    /* base00 */
	font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: 1em;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}

.codeblock-theme-solarizedlight pre[class*="language-"]::-moz-selection,.codeblock-theme-solarizedlight  pre[class*="language-"] ::-moz-selection,.codeblock-theme-solarizedlight 
code[class*="language-"]::-moz-selection,.codeblock-theme-solarizedlight  code[class*="language-"] ::-moz-selection {
    background: #073642;
    /* base02 */;
}

.codeblock-theme-solarizedlight pre[class*="language-"]::selection,.codeblock-theme-solarizedlight  pre[class*="language-"] ::selection,.codeblock-theme-solarizedlight 
code[class*="language-"]::selection,.codeblock-theme-solarizedlight  code[class*="language-"] ::selection {
    background: #073642;
    /* base02 */;
}

/* Code blocks */
.codeblock-theme-solarizedlight pre[class*="language-"] {
    padding: 1em;
    margin: .5em 0;
    overflow: auto;
    border-radius: 0.3em;
}

.codeblock-theme-solarizedlight :not(pre) > code[class*="language-"],.codeblock-theme-solarizedlight 
pre[class*="language-"] {
    background-color: #fdf6e3;
    /* base3 */;
}

/* Inline code */
.codeblock-theme-solarizedlight :not(pre) > code[class*="language-"] {
    padding: .1em;
    border-radius: .3em;
}

.codeblock-theme-solarizedlight .token.comment,.codeblock-theme-solarizedlight 
.token.prolog,.codeblock-theme-solarizedlight 
.token.doctype,.codeblock-theme-solarizedlight 
.token.cdata {
    color: #93a1a1;
    /* base1 */;
}

.codeblock-theme-solarizedlight .token.punctuation {
    color: #586e75;
    /* base01 */;
}

.codeblock-theme-solarizedlight .token.namespace {
    opacity: .7;
}

.codeblock-theme-solarizedlight .token.property,.codeblock-theme-solarizedlight 
.token.tag,.codeblock-theme-solarizedlight 
.token.boolean,.codeblock-theme-solarizedlight 
.token.number,.codeblock-theme-solarizedlight 
.token.constant,.codeblock-theme-solarizedlight 
.token.symbol,.codeblock-theme-solarizedlight 
.token.deleted {
    color: #268bd2;
    /* blue */;
}

.codeblock-theme-solarizedlight .token.selector,.codeblock-theme-solarizedlight 
.token.attr-name,.codeblock-theme-solarizedlight 
.token.string,.codeblock-theme-solarizedlight 
.token.char,.codeblock-theme-solarizedlight 
.token.builtin,.codeblock-theme-solarizedlight 
.token.url,.codeblock-theme-solarizedlight 
.token.inserted {
    color: #2aa198;
    /* cyan */;
}

.codeblock-theme-solarizedlight .token.entity {
    color: #657b83;
    /* base00 */
	background: #eee8d5;
    /* base2 */;
}

.codeblock-theme-solarizedlight .token.atrule,.codeblock-theme-solarizedlight 
.token.attr-value,.codeblock-theme-solarizedlight 
.token.keyword {
    color: #859900;
    /* green */;
}

.codeblock-theme-solarizedlight .token.function,.codeblock-theme-solarizedlight 
.token.class-name {
    color: #b58900;
    /* yellow */;
}

.codeblock-theme-solarizedlight .token.regex,.codeblock-theme-solarizedlight 
.token.important,.codeblock-theme-solarizedlight 
.token.variable {
    color: #cb4b16;
    /* orange */;
}

.codeblock-theme-solarizedlight .token.important,.codeblock-theme-solarizedlight 
.token.bold {
    font-weight: bold;
}

.codeblock-theme-solarizedlight .token.italic {
    font-style: italic;
}

.codeblock-theme-solarizedlight .token.entity {
    cursor: help;
}

